import React, { Component } from 'react';
import { Button, Form, Grid, Header, Message, Segment,Select } from 'semantic-ui-react'
import {Link} from 'react-router-dom';

class Signup extends Component{
    state = {
        data : {
            nombre : '',
            apellido :'',
            password : '',
            edad : '',
            gender : ''
        },
        errors : {}
    };

    saveUser = (newUser)=>{
        const {socket} = this.props;
        socket.emit('signUp',newUser,(data)=>{
            if(data){
                console.log(data)
                this.props.history.push('/');
            }
        });
    }
    handleSubmit = (e) => {
        e.preventDefault();
        const errors = this.validate(this.state.data);
        this.setState({ errors });
		if (Object.keys(errors).length === 0) {
            this.saveUser(this.state.data);
        }
    };
    validate = (data) => {
		const errors = {};
        if (!data.nombre) errors.nombre = 'El Nombre es Requerido.';
        if (!data.apellido) errors.apellido = 'El Apellido es Requerido.';
        if (!data.password) errors.password = 'La contraseña es Requerida.';
       // if(data.password.length < 8) errors.password = 'La contraseña debe ser mínimo de 8 Caracteres';
        if (!data.gender) errors.gender = 'el Género es Requerido.';
        if (!data.edad) errors.edad = 'la Edad es Requerida.';
		return errors;
	};

    handleStringChange = (e) => {
		this.setState({
			data: { ...this.state.data, [e.target.name]: e.target.value }
		});
    };
    handleGenderChange = (e,data)=>{
        this.setState({
			data: { ...this.state.data, [data.name]: data.value }
		});
    }
    render(){
        const { data,errors} = this.state;
        const options = [
            { key: 'm', text: 'Hombre', value: 'hombre' },
            { key: 'f', text: 'Mujer', value: 'mujer' },
            { key: 'o', text: 'Otro', value: 'otro' }
          ]
        let errorsList = [];
        if(Object.keys(errors).length !== 0){     
            Object.keys(errors).forEach(
                key=> errorsList.push(errors[key]));
        }
        return(
            <div className='login-form'>
                <style>{`
                    body > div,
                    body > div > div,
                    body > div > div > div.login-form {
                        height: 100%;
                    }
                    `}
                </style>
                <Grid textAlign='center' style={{ height: '90%' }} verticalAlign='middle'>
                    <Grid.Column style={{ maxWidth: 450}}>
                        <Header as='h2' color='teal' textAlign='center'>
                         Sign-up
                        </Header>
                        <Form size='large' error={Object.keys(errors).length !== 0 ? true : false} onSubmit={this.handleSubmit}>
                        <Segment stacked>
                        <div>
                            <Form.Input fluid
                                type="text"
                                id="nombre"
                                name="nombre"
                                icon='user' iconPosition='left' 
                                placeholder='Nombre'
                                error={errors.nombre ? true : false}
                                value={data.nombre}
                                onChange={this.handleStringChange} />
                        </div>
                        <div>
                            <Form.Input fluid
                                type="text"
                                id="apellido"
                                name="apellido"
                                icon='user' iconPosition='left' 
                                placeholder='Apellido'
                                error={errors.apellido ? true : false}
                                value={data.apellido}
                                onChange={this.handleStringChange} />
                        </div>
                        <div>
                            <Form.Input
                                fluid
                                icon='lock'
                                iconPosition='left'
                                placeholder='Password'
                                type='password'
                                id="password"
                                name="password"
                                error={errors.password ? true : false}
                                value={data.password}
                                onChange={this.handleStringChange}
                            />
                        </div>
                        <div>
                            <Form.Input
                                fluid
                                icon='male'
                                iconPosition='left'
                                placeholder='Edad'
                                type='number'
                                min='18'
                                max='70'
                                id="edad"
                                name="edad"
                                error={errors.edad ? true : false}
                                value={data.edad}
                                onChange={this.handleStringChange}
                                />
                        </div>
                        <div>
                            <Form.Field
                                fluid
                                control={Select}  
                                id="gender"
                                name="gender"
                                error={errors.gender ? true : false}
                                options={options} 
                                onChange={this.handleGenderChange}
                                placeholder='Gender' />
                        </div>
                        <div style={{margin : '15 0'}}>
                                {
									Object.keys(errors).length !== 0 ?
									<Message
										error
										header='Se han producido errores'
										list={errorsList}
									/> : <div></div>
								}
                        </div>
                            <Button color='teal' fluid size='large' type="submit">
                            Sign-Up
                            </Button>
                        </Segment>
                        </Form>
                        <Message>
                        Already have an account with us? <Link to='/'>Login</Link>
                        </Message>
                    </Grid.Column>
                </Grid>
            </div>
        )
    }
}

export default Signup;