import React, { Component } from 'react';
import { Button, Form, Grid, Header, Message, Segment } from 'semantic-ui-react'
import {Link} from 'react-router-dom';

class Login extends Component{
    state = {
		data: {
			username: '',
			password: '',	
		},
		errors: {}
    };

    login = (user)=>{
        const {socket} = this.props;
        socket.emit('login',user,(data)=>{
            let {user} = data
            if(user){
                this.props.setUser(data);
                this.props.history.push('/chat');
            }else{
                this.setState({errors : {err : data.err}});
            }
        });
        
    }
    validate = (data) => {
		const errors = {};
		if (!data.username) errors.username = 'Username is Required.';
		if (!data.password) errors.password = 'Password is required.';
		return errors;
	};
    handleSubmit = (e) => {
        e.preventDefault();
        const errors = this.validate(this.state.data);
        this.setState({ errors });
		if (Object.keys(errors).length === 0) {
            this.login(this.state.data);
        }
	};
    handleStringChange = (e) => {
		this.setState({
			data: { ...this.state.data, [e.target.name]: e.target.value }
		});
	};
    render(){
        const { data,errors} = this.state;
        let errorsList = [];
		if(Object.keys(errors).length !== 0){
			
			Object.keys(errors).forEach(
				key=> errorsList.push(errors[key]));
		}
        return(
            <div className='login-form'>
                <style>{`
                    body > div,
                    body > div > div,
                    body > div > div > div.login-form {
                        height: 100%;
                    }
                    `}
                </style>
                <Grid textAlign='center' style={{ height: '100%' }} verticalAlign='middle'>
                    <Grid.Column style={{ maxWidth: 450}}>
                        <Header as='h2' color='teal' textAlign='center'>
                         Log-in to your account
                        </Header>
                        <Form size='large' error={Object.keys(errors).length !== 0 ? true : false} onSubmit={this.handleSubmit}>
                        <Segment stacked>
                            <div>
                                <Form.Input fluid
                                    required
                                    type="text"
                                    id="username"
                                    name="username"
                                    icon='user' iconPosition='left' 
                                    placeholder='Username'
                                    error={ errors.username ? true : false}
                                    value={data.username}
                                    onChange={this.handleStringChange} />
                                     
                            </div>
                            <div>
                                <Form.Input
                                    fluid
                                    required
                                    icon='lock'
                                    iconPosition='left'
                                    placeholder='Password'
                                    type='password'
                                    id="password"
                                    name="password"
                                    error={errors.password ? true : false}
                                    value={data.password}
                                    onChange={this.handleStringChange}
                                />
                            </div>
                            <div style={{margin : '15 0'}}>
                                {
									Object.keys(errors).length !== 0 ?
									<Message
										error
										header='Se han producido errores'
										list={errorsList}
									/> : <div></div>
								}
                            </div>
                            <Button color='teal' fluid size='large' type="submit">
                            Login
                            </Button>
                        </Segment>
                        </Form>
                        <Message>
                        New to us? <Link to='/signup'>Sign up</Link>
                        </Message>
                    </Grid.Column>
                </Grid>
            </div>
        )
    }
}

export default Login;


