import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import { Container,Header } from 'semantic-ui-react'
import openSocket from 'socket.io-client'

import Login from './loginForm';
import SignUp from './signupForm';
import Chat from './chat';
import AdminRoute from './adminRoute';


class Layout extends Component{
    state = {
        socket : null,
        user : null
    }
    async componentWillMount(){
        this.initSocket();
    }
    initSocket =()=>{
        const socket = openSocket("http://192.168.1.53:5000");
        
        socket.on('connect',()=>{
            console.log("connected");
        })
        this.setState({socket});
    }

    setUser = (user)=>{
        this.setState({user});   
    }

    /**logout = ()=>{
        const {socket} = this.state;
        socket.emit(LOGOUT);
        this.setState({user:null});
    **/
    render(){
        const title = this.props.title;
        return(
            <div>
                <Container text style={{ marginTop: '2em',marginBottom: '2em' }}>
                    <Header textAlign='center' as='h1'>{title}</Header>
                </Container>
                

                 <div>
                    <Route exact path='/' render={props =>(<Login {...props } setUser={this.setUser} socket={this.state.socket}/>)}/>
                    <Route exact path='/signup' render={props => (<SignUp {...props} socket={this.state.socket} />)}/>
                    <AdminRoute exact path='/chat' 
                        user={this.state.user}
                        component={props=>(<Chat {...props} user={this.state.user}/>)} />
                </div>
            </div>
        )
        
    }
}

export default Layout;

